import React, { useState } from "react";
import { PowerOff, Power, Pencil, Trash } from "lucide-react";
import { Button } from "@/components/ui/button";
import { useRouter } from "next/router";

type School = {
  id: string;
  name: string;
  email: string;
  createdAt: string;
  status?: string;
};

interface SchoolTableProps {
  schools: School[];
}

const SchoolTable: React.FC<SchoolTableProps> = ({ schools }) => {
  const [editSchool, setEditSchool] = useState<School | null>(null);
  const [editName, setEditName] = useState("");
  const router = useRouter();

  const handleEditSave = async () => {
    // TODO: Call your API to update the school here
    // await fetch(...);
    setEditSchool(null);
  };

  return (
    <div className="overflow-x-auto rounded-xl border border-gray-200 shadow-sm">
      <table className="min-w-full text-sm text-left">
        <thead className="bg-gray-50 text-gray-600 uppercase text-xs">
          <tr>
            <th className="px-6 py-4">ឈ្មោះ</th>
            <th className="px-6 py-4">អ៊ីមែល</th>
            <th className="px-6 py-4">ថ្ងៃចុះឈ្មោះ</th>
            <th className="px-6 py-4">ស្ថានភាព</th>
            <th className="px-6 py-4">សកម្មភាព</th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-100 bg-white">
          {(schools ?? []).map((school) => (
            <tr key={school.id}>
              <td className="px-6 py-4 flex items-center gap-3">
                {/* Avatar */}
                <div className="w-8 h-8 rounded-full bg-blue-100 text-blue-700 flex items-center justify-center font-bold">
                  {school.name?.charAt(0).toUpperCase()}
                </div>
                <span>{school.name}</span>
              </td>
              <td className="px-6 py-4">{school.email}</td>
              <td className="px-6 py-4">
                {new Date(school.createdAt).toLocaleDateString("en-US", {
                  month: "short",
                  day: "numeric",
                  year: "numeric",
                })}
              </td>
              <td className="px-6 py-4">
                {school.status !== "deactivated" ? (
                  <span className="bg-green-100 text-green-700 px-2 py-1 rounded">សកម្ម</span>
                ) : (
                  <span className="bg-red-100 text-red-700 px-2 py-1 rounded">អសកម្ម</span>
                )}
              </td>
              <td className="px-6 py-4 flex items-center gap-2">
                {school.status !== "deactivated" ? (
                  <Button className="bg-yellow-100 text-yellow-700 px-2 py-1 rounded mr-2 flex items-center gap-1">
                    <PowerOff size={16} /> 
                  </Button>
                ) : (
                  <Button className="bg-blue-100 text-blue-700 px-2 py-1 rounded mr-2 flex items-center gap-1">
                    <Power size={16} /> 
                  </Button>
                )}
                <Button
  className="bg-gray-100 text-gray-700 px-2 py-1 rounded mr-2 flex items-center gap-1"
  onClick={() => router.push(`/admin/edit/${school.id}`)}
>
  <Pencil size={16} /> 
</Button>
                <Button className="bg-red-100 text-red-700 px-2 py-1 rounded flex items-center gap-1">
                  <Trash size={16} /> 
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Edit Modal */}
      {editSchool && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-md w-[90%] max-w-md">
            <h2 className="font-semibold text-lg mb-4">Edit School</h2>
            <input
              className="w-full px-4 py-2 border rounded mb-4"
              value={editName}
              onChange={e => setEditName(e.target.value)}
            />
            <div className="flex gap-2">
              <Button
                className="bg-[#25388C] hover:bg-[#1e2e6d] text-white flex-1"
                onClick={handleEditSave}
              >
                Save
              </Button>
              <Button
                variant="secondary"
                className="flex-1"
                onClick={() => setEditSchool(null)}
              >
                Cancel
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default React.memo(SchoolTable);